.contextMenuContainer {
    /* width: 100px; */
    /* background: #559000; */
    /* color: #fff; */
    height: calc(100vh - 600px);
    overflow: auto;
    display: flex;
    position: fixed;
    border-radius: 5px;
    transform-origin: top left;
    z-index: 10000;
  }
  
    .contextMenuContainer.visible {
      transition: transform 200ms ease-in-out;
    }
  
  .menu-link {
    cursor: pointer;
    height: 23px;
    background: #559000;
    color: #fff;
    font-size: 11px;
  /*  
    border-radius: 5px;
    padding: 2px 6px; */
    font-family: "Museo Sans", sans-serif;
  }
  
    .menu-link:hover {
      /* background: #559000; */
    }
  
  .menuIcon {
    /* margin-right: 7px; */
  }
  .item-action {
      float: right;
      z-index: 1001;
      margin: 2px 2px 0 0;
      border: 1px solid #ccc;
      min-width: 30px;
      font-size: 13px;
      text-align: center;
  }

  .list-group-item-success{
   // background-color: #fff !important;
  }

  .form-view {
    position: relative;
  }
  
  .leftcol {
    min-height: 550px;
    float: left;
    padding: 0px;
    position: sticky;
    top: 0;
  }
  
  .leftlayout {
    height: 30px;
    border: 1px solid #ddd;
    margin-left: 7px;
    padding: 4px;
    font-size: 13px;
    margin-right: 7px;
  }
  
  .leftlayoutdmotyp {
    height: 415px;
    border: 1px solid #ddd;
    margin-right: 5px;
    padding: 6px;
    font-size: 15px;
  }
  
  .leftlayout1 {
    height: 30px;
    border-bottom: 1px solid #ddd;
    font-size: 13px;
  }
  
  .formcomponents {
    overflow-y: auto;
    max-height: 358px;
    overflow-x: hidden;
  }
  
  div.scroll-list {
    overflow: auto;
    max-height: 70vh;
  }
  
  
  /* The Close Button */
  .close {
    color: white;
    float: right;
    font-size: 28px;
    font-weight: bold;
  }
  
  .close:hover,
  .close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
  }
  
  .list-group {
    li {
      font-size: 12px !important;
      // background-color: transparent !important;
      padding: 7px !important;
      cursor: pointer;
    }
  }
  
  .formarea {
    visibility: visible;
    position: relative;
    min-height: 450px;
    padding: 10px;
    border: dotted 2px #e8e8e8;
    float: right;
  }
  
  
  .col {
    float: left;
  }
  
  .alert {
    border-radius: 0;
    padding: 15px;
    margin-bottom: 20px;
    border: 1px solid transparent;
    text-align: center;
    font-size: 12px;
  }
  
  .alert-info {
    color: #31708f;
    background-color: #d9edf7;
    border-color: #bce8f1;
    margin: 10px 20px;
  }
  
  .dmo-columns {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 5px;
    margin-bottom: 5px;
    & > * {
      align-self: self-start;
    }
    .span-2 {
      grid-column: span 2;
    }
  }
  
  .cdk-drop-list:not(#form-controls) {
    border: 2px dotted #e8e8e8;
    transform: translate3d(0,0,0);
  }
  
  .cdk-drag-preview {
    box-sizing: border-box;
    border-radius: 4px;
    box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2),
                0 8px 10px 1px rgba(0, 0, 0, 0.14),
                0 3px 14px 2px rgba(0, 0, 0, 0.12);
  }
  
  .cdk-drag-animating {
    transition: transform 250ms cubic-bezier(0, 0, 0.2, 1);
    transform: translate3d(0,0,0);
  }
  
  .drag-list-item:last-child {
    border: none;
  }
  
  .drag-list-item {
    display: flex;
    align-items: center;
    cursor: move;
    background: white;
    padding: 0 20px;
  }
  
  .cdk-drag-placeholder {
    opacity: 0;
  }
  
  .cdk-drop-list-receiving:not(#form-controls) {
    border: #4c9e00 dotted 2px;
  }
  
  
  
  .drag-list.cdk-drop-list-dragging .drag-list-item {
    transition: transform 250ms cubic-bezier(0, 0, 0.2, 1);
    min-width: 250px;
    transform: translate3d(0,0,0);
  }
  
  .item-actions {
    background-color: #FFF;
    position: absolute;
    z-index: 999;
    right: 15px;
    top: -22px;
    display: none;
  
    &.active {
      display: block;
    }
    &.dmo-actions {
      top: 0;
    }
  }
  
  .item-action {
    float: right;
    z-index: 1001;
    margin: 2px 2px 0 0;
    border: 1px solid #ccc;
    min-width: 30px;
    font-size: 13px;
    text-align: center;
  }